
import Swiper from 'vux/src/components/swiper/swiper.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import i_one from '../../assets/twoshiyi.png'
import i_two from '../../assets/TheShapeofVoice.jpg'

const list = () => ['精选', '美食', '电影', '酒店', '外卖']

const imgLists =[] ;
const demoList = imgLists.map((imgList, index) => ({
img: imgList.img
}))

export default {
data() {
return {
demo03_list: demoList,
list2: list(),
demo2: '美食',
list3: ['推荐课程', '推荐题库','新闻资讯'],
demo3: '收到的消息',
list4: ['正在放映', '即将上映'],
demo4: '即将上映',
demoDisabled: 'A',
index: 0,
getBarWidth: function (index) {
return (index + 1) * 22 + 'px'
}
}
},

components: {
Swiper,
SwiperItem,
GroupTitle,
XButton,
Divider,
Tab,
TabItem
},
methods: {
onItemClick(index) {
console.log('on item click:', index)
},
addTab() {
if (this.list2.length < 5) {
this.list2 = list().slice(0, this.list2.length + 1)
}
},
removeTab() {
if (this.list2.length > 1) {
this.list2 = list().slice(0, this.list2.length - 1)
}
},
next() {
if (this.index === this.list2.length - 1) {
this.index = 0
} else {
++this.index
}
},
prev() {
if (this.index === 0) {
this.index = this.list2.length - 1
} else {
--this.index
}
}
},
mounted(){
var url=this.imgLists
this.$axios.post("http://192.168.3.7:8080/front/advertisement/findByPositionId?id=1",{

imageUrl:url



}).then(response=>{

}).catch(err=>{

})
}
}




