import Qs from 'qs'

export default {

  data() {
    return {
      code: 0,
      paw: '',
      phone: ''
    }
  },
  methods: {
    login() {
      var phone = this.phone;
      var paw = this.paw;

      if (phone == "" || paw == "") {
        alert("请输入密码或者账号");

      }
      var data = ({
        phone: phone,
        password: paw
      })
      this.$axios.post("http://192.168.3.7:8080/front/login.json", {
      
         phone: phone,
        password: paw
       
      }, {
          header: {
            "Content-Type": "application/json;charset=utf-8"
          },
          withCredentials: true
        }).then(response => {

          this.code = response.data.code;

          if (this.code = 100) {
            alert("登陆成功");
            this.$router.push({
              path: '/gerenindex'
            })
          }
        }).catch(response => {
          console.log(response);
        })
    }
  }
}



