import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import gerenIndex from '../components/my/gerenIndex.vue'
import account from '../components/my/account.vue'
import clasroom from '../components/my/classroom.vue'
import order from '../components/my/order.vue'
import recharge from '../components/my/recharge.vue'
import selected from '../components/selected/selected.vue'
import examination from '../components/selected/examination.vue'
import questions from '../components/questions/questions.vue'
import questionsList from '../components/questions/questionsList.vue'
import subject from '../components/questions/subject.vue'

Vue.use(Router)


export default new Router({
  routes: [
     {
      path: '/',
      name: 'Hello',
      component: Hello
    },{
      path:'/gerenIndex',
      component:gerenIndex
    },{
      path:'/account',
      component:account
    },{
      path:'/clasroom',
      component:clasroom
    },{
      path:'/order',
      component:order
    },{
      path:'/recharge',
      component:recharge
    },{
      path:'/selected',
      component:selected
    },{
      path:'/examination',
      component:examination
    },{
      path:'/questions',
      component:questions
    },{
      path:'/questionsList',
      component:questionsList
    },{
      path:'/subject',
      component:subject
    }
  ]
})



